a {
  -webkit-transition: all 0.25s ease;
  -moz-transition: all 0.25s ease;
  -ms-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
}

a:hover {
  color: #00b5ad;
}

.word-break-all {
  word-break: break-all;
}

.white-space-nowrap {
  white-space: nowrap;
}

.masthead.segment {
  min-height: 700px;
  padding: 1em 0em;
}

.masthead h1.ui.header {
  margin-top: 1.8em;
  margin-bottom: 0em;
  font-size: 5em;
  font-weight: normal;
}

.masthead h2 {
  font-size: 1.7em;
  font-weight: normal;
}

.ui.vertical.stripe {
  padding: 6em 0em;
}

.ui.success.message,
.ui.error.message {
  margin: 3em 0em;
}

#shorten {
  font-size: 0.8em;
  font-weight: bold;
}

#loading {
  margin: 1em 0;
}

.message h1 {
  font-size: 2em;
}

.footer.segment {
  font-size: 1.4em;
  font-weight: normal;
  padding: 2em 0em;
}

@media only screen and (max-width: 700px) {
  .masthead.segment {
    min-height: 700px;
    padding: 1em 0em;
  }

  .masthead h1.ui.header {
    margin-top: 3.6em;
    margin-bottom: 0em;
    font-size: 3.2em;
    font-weight: normal;
  }

  .masthead h2 {
    font-size: 1.4em;
    font-weight: normal;
  }

  .ui.vertical.stripe {
    padding: 6em 0em;
  }

  .ui.success.message,
  .ui.error.message {
    margin: 3em 0em;
  }

  #shorten {
    font-size: 1em;
    font-weight: normal;
  }

  #loading {
    margin: 4em 0;
  }

  #loading img {
    width: 120px;
  }

  .message h1 {
    font-size: 1.6em;
  }

  .footer.segment {
    font-size: 1.2em;
    font-weight: normal;
    padding: 2em 0em;
  }
}




